  .parent-wrappe {
    position: relative;
    width: 500px;
    height: 761px;
    margin: 40px auto 0;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-y: -600%;
    background: #2b5876;
    border-radius: 30px;
    color: #FFF;
    box-shadow: 0px 0px 50px 5px rgba(0, 0, 0, 0.5);
  }
  .parent-wrappe-activity {
    position: relative;
    width: 63vh;
    height: 127vh;
    margin: 40px auto 0;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-y: -600%;
    background: #2b5876;
    border-radius: 30px;
    color: #FFF;
    box-shadow: 0px 0px 50px 5px rgba(0, 0, 0, 0.5);
  }
  .parent-wrappe h3 {
      text-align: center;
  }
  .close-btn {
    margin: 20px;
    font-size: 18px;
    cursor: pointer;
    background-color: white;
  }
label{
  text-align: right;
  display:inherit;

}
  .parent-wrappe  input{
    float: right;
}
.parent-wrappe  select{
    float: right;
}
.to-the-right{
float: right;
}
.parent-wrappe input{
    float:right;
}
  .subscribe-wrappe {
    position: absolute;
    left: -30px;
    right: -30px;
    height: 92%;
    padding: 30px;
    background-position-x: 272%;
    background-position-y: -1px;
    background-repeat: no-repeat;
    background-color: #FFF;
    border-radius: 18px;
    color: #333;
    box-shadow: 0px 0px 60px 5px  
  }
  
  .subscribe-wrappe:after {
    position: absolute;
    content: "";
    right: -10px;
    bottom: 71px;
    width: 0;
    height: 0;
    border-left: 0px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #7149c7;
  }
  
  .subscribe-wrappe h4 {
    text-align: center;
    font-size: 20px;
    font-weight: bold;
    letter-spacing: 3px;
    line-height: 28px;
  }
  
  .subscribe-wrappe input {
    bottom: 30px;
    border: none;
    border-bottom: 1px solid #d4d4d4;
    padding: 10px;
    width: 90%;
    background: transparent;
    transition: all .25s ease;
  }
 
  .subscribe-wrappe input:focus {
    outline: none;
    border-bottom: 1px solid #a77cf4;
  }
    
  .subscribe-wrappe  select
  {
    bottom: 30px;
    border: none;
    border-bottom: 1px solid #d4d4d4;
    padding: 10px;
    width: 65%;
    background: transparent;
    transition: all .25s ease;
  }
  
  .subscribe-wrappe   select:focus {
    outline: none;
    border-bottom: 1px solid #a77cf4;
  }
  .subscribe-wrappe .submit-btn {
    position: absolute;
    border-radius: 30px;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    background-color: #927cf4;
    color: #FFF;
    padding: 12px 25px;
    display: inline-block;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 2px;
    right: -10px;
    bottom: 30px;
    cursor: pointer;
    transition: all .25s ease;
    box-shadow: -5px 6px 20px 0px ;
  }
  .subscribe-wrappe .back-arr {
    position: absolute;
    border-radius: 30px;
    border-bottom-left-radius: 0;
    border-top-left-radius: 0;
    background-color: #927cf4;
    color: #FFF;
    padding: 12px 25px;
    display: inline-block;
    font-size: 12px;
    font-weight: bold;
    letter-spacing: 2px;
    left: -10px;
    bottom: 30px;
    cursor: pointer;
    transition: all .25s ease;
    box-shadow: -5px 6px 20px 0px ;
  }
  .subscribe-wrappe .submit-btn:hover {
    background-color: #8e62dc;
  }
  
  .parent-wrappe .material-icons{
margin-top: 50px; }

.pointer {cursor: pointer;}




